const english_verb = function (verb: any) {
  if (verb.infinitive.split(" ")[1] !== "be") {
    return {
      participle: verb.conj.participle,
      gerund: verb.conj.gerund,
      indicative: {
        positive: [
          verb.conj.indicative.positive,
          "will " + verb.conj.indicative.positive,
        ],
        negative: [
          verb.conj.indicative.negative,
          "won't " + verb.conj.indicative.positive,
        ],
      },
      past_indicative: {
        positive: [verb.conj.past_indicative.positive],
        negative: [verb.conj.past_indicative.negative],
      },
      presumptive: {
        positive: [
          "probably " + verb.conj.indicative.positive,
          "will probably " + verb.conj.indicative.positive,
        ],
        negative: [
          "probably don't " + verb.conj.indicative.positive,
          "probably won't " + verb.conj.indicative.positive,
        ],
      },
      past_presumptive: {
        positive: [
          "probably " + verb.conj.past_indicative.positive,
          "probably have " + verb.conj.participle,
          "probably had " + verb.conj.participle,
        ],
        negative: [
          "probably didn't " + verb.conj.indicative.positive,
          "probably haven't " + verb.conj.participle,
          "probably hadn't " + verb.conj.participle,
        ],
      },
      progressive: {
        positive: ["are " + verb.conj.gerund],
        negative: ["aren't " + verb.conj.gerund],
      },
      past_progressive: {
        positive: ["were " + verb.conj.gerund],
        negative: ["weren't " + verb.conj.gerund],
      },
      volitional: {
        positive: [
          "intend to " + verb.conj.indicative.positive,
          "will " + verb.conj.indicative.positive,
          "let's " + verb.conj.indicative.positive,
        ],
        negative: [
          "don't intend to " + verb.conj.indicative.positive,
          "won't " + verb.conj.indicative.positive,
          "let's not " + verb.conj.indicative.positive,
        ],
      },
      potential: {
        positive: [
          "can " + verb.conj.indicative.positive,
          "are able " + verb.infinitive,
        ],
        negative: [
          "can't " + verb.conj.indicative.positive,
          "aren't able " + verb.infinitive,
        ],
      },
      causative: {
        positive: [
          "make X " + verb.conj.indicative.positive,
          "let X " + verb.conj.indicative.positive,
        ],
        negative: [
          "don't make X " + verb.conj.indicative.positive,
          "don't let X " + verb.conj.indicative.positive,
        ],
      },
      passive: {
        positive: [
          "are " + verb.conj.participle,
          "will be " + verb.conj.participle,
        ],
        negative: [
          "aren't " + verb.conj.participle,
          "won't be " + verb.conj.participle,
        ],
      },
      provisional: {
        positive: ["if X " + verb.conj.indicative.positive],
        negative: ["if X don't " + verb.conj.indicative.positive],
      },
      conditional: {
        positive: [
          "if X " + verb.conj.indicative.positive,
          "when X " + verb.conj.indicative.positive,
        ],
        negative: [
          "if X don't " + verb.conj.indicative.positive,
          "when X don't " + verb.conj.indicative.positive,
        ],
      },
      imperative: {
        positive: [verb.conj.indicative.positive],
        negative: [verb.conj.indicative.negative],
      },
      request: {
        positive: ["please " + verb.conj.indicative.positive],
        negative: ["please " + verb.conj.indicative.negative],
      },
    };
  } else {
    const stem = verb.infinitive.split(" ").splice(1).join(" ");
    return {
      participle: verb.conj.participle,
      gerund: verb.conj.gerund,
      indicative: {
        positive: [verb.conj.indicative.positive, "will " + stem],
        negative: [verb.conj.indicative.negative, "won't " + stem],
      },
      past_indicative: {
        positive: [verb.conj.past_indicative.positive],
        negative: [verb.conj.past_indicative.negative],
      },
      presumptive: {
        positive: [
          "probably " + verb.conj.indicative.positive,
          "will probably " + stem,
        ],
        negative: [
          "probably " + verb.conj.indicative.negative,
          "probably won't " + stem,
        ],
      },
      past_presumptive: {
        positive: [
          "probably " + verb.conj.past_indicative.positive,
          "probably have " + verb.conj.participle,
          "probably had " + verb.conj.participle,
        ],
        negative: [
          "probably " + verb.conj.past_indicative.negative,
          "probably haven't " + verb.conj.participle,
          "probably hadn't " + verb.conj.participle,
        ],
      },
      progressive: {
        positive: ["are " + verb.conj.gerund],
        negative: ["aren't " + verb.conj.gerund],
      },
      past_progressive: {
        positive: ["were " + verb.conj.gerund],
        negative: ["weren't " + verb.conj.gerund],
      },
      volitional: {
        positive: [
          "intend " + verb.infinitive,
          "will " + stem,
          "let's " + stem,
        ],
        negative: [
          "don't intend " + verb.infinitive,
          "won't " + stem,
          "let's not " + stem,
        ],
      },
      potential: {
        positive: ["can " + stem, "are able " + verb.infinitive],
        negative: ["can't " + stem, "aren't able " + verb.infinitive],
      },
      causative: {
        positive: ["make X " + stem, "let X " + stem],
        negative: ["don't make X " + stem, "don't let X " + stem],
      },
      passive: {
        positive: [
          "suffer X " + verb.conj.gerund,
          "will suffer X " + verb.conj.gerund,
        ],
        negative: [
          "don't suffer X " + verb.conj.gerund,
          "won't suffer X " + verb.conj.gerund,
        ],
      },
      provisional: {
        positive: ["if X " + verb.conj.indicative.positive],
        negative: ["if X " + verb.conj.indicative.negative],
      },
      conditional: {
        positive: [
          "if X were " + verb.infinitive,
          "when X " + verb.conj.indicative.positive,
        ],
        negative: [
          "if X weren't " + verb.infinitive,
          "when X " + verb.conj.indicative.negative,
        ],
      },
      imperative: {
        positive: [stem],
        negative: ["don't " + stem],
      },
      request: {
        positive: ["please " + stem],
        negative: ["please don't " + stem],
      },
    };
  }
};

